// https://docs.thorchain.org/how-it-works/governance#mimir

import { useMemo } from 'react'

import { Amount, SupportedChain } from 'multichain-sdk'

import { useMidgard } from 'redux/midgard/hooks'

export const useMimir = () => {
  const { networkData, mimir } = useMidgard()
  const maxLiquidityRuneMimir = mimir?.['mimir//MAXIMUMLIQUIDITYRUNE']
  const haltETHTrading = mimir?.['mimir//HALTETHTRADING'] !== 0
  const haltBTCTrading = mimir?.['mimir//HALTBTCTRADING'] !== 0
  const haltLTCTrading = mimir?.['mimir//HALTLTCTRADING'] !== 0
  const haltTrading = mimir?.['mimir//HALTTRADING'] !== 0
  const haltTHORChain = mimir?.['mimir//HALTTHORCHAIN'] !== 0
  const haltBCHTrading = mimir?.['mimir//HALTBCHTRADING'] !== 0
  const haltBNBTrading = mimir?.['mimir//HALTBNBTRADING'] !== 0
  const haltETHChain = mimir?.['mimir//HALTETHCHAIN'] !== 0
  const haltBTCChain = mimir?.['mimir//HALTBTCCHAIN'] !== 0
  const haltLTCChain = mimir?.['mimir//HALTLTCCHAIN'] !== 0
  const haltBCHChain = mimir?.['mimir//HALTBCHCHAIN'] !== 0
  const haltBNBChain = mimir?.['mimir//HALTBNBCHAIN'] !== 0
  const haltedChains = useMemo(
    (): Record<SupportedChain, boolean> => ({
      THOR: haltTHORChain || haltTrading,
      ETH: haltETHTrading || haltETHChain || haltTrading,
      BTC: haltBTCTrading || haltBTCChain || haltTrading,
      LTC: haltLTCTrading || haltLTCChain || haltTrading,
      BCH: haltBCHTrading || haltBCHChain || haltTrading,
      BNB: haltBNBTrading || haltBNBChain || haltTrading,
    }),
    [
      haltTrading,
      haltTHORChain,
      haltETHChain,
      haltLTCChain,
      haltBCHChain,
      haltBNBChain,
      haltBTCChain,
      haltETHTrading,
      haltLTCTrading,
      haltBCHTrading,
      haltBNBTrading,
      haltBTCTrading,
    ],
  )

  const maxLiquidityRune = Amount.fromMidgard(maxLiquidityRuneMimir)
  const totalPooledRune = Amount.fromMidgard(networkData?.totalPooledRune)

  const isFundsCapReached: boolean = useMemo(() => {
    if (!maxLiquidityRuneMimir) return false

    // totalPooledRune >= maxLiquidityRune - 100k RUNE
    return maxLiquidityRune
      .sub(Amount.fromMidgard(100000 * 10 ** 8))
      .lte(totalPooledRune)
  }, [totalPooledRune, maxLiquidityRune, maxLiquidityRuneMimir])

  const capPercent = useMemo(() => {
    if (!maxLiquidityRuneMimir) return null

    const poolLimit = maxLiquidityRune.sub(Amount.fromMidgard(100000 * 10 ** 8))

    return `${totalPooledRune.div(poolLimit).mul(100).toFixed(1)}%`
  }, [totalPooledRune, maxLiquidityRune, maxLiquidityRuneMimir])

  return {
    haltedChains,
    totalPooledRune,
    maxLiquidityRune,
    isFundsCapReached,
    capPercent,
  }
}
